<template>
  <div class="student-item">

    <el-form :inline="true" class="demo-form-inline"  size="mini">

      <el-form-item>
        <el-button-group>
          <el-button @click="  download_template  " size="mini"></el-button>

        </el-button-group>

      </el-form-item>
      <el-form-item>
        <input type="file" @change="onchange" value=""/>
      </el-form-item>
     <el-form-item>
        <el-button @click="  upload  " size="mini"></el-button>
      </el-form-item>

    </el-form>
    <el-form>
      <el-form-item label="">{{total}}</el-form-item>
      <el-form-item label="">{{success}}</el-form-item>
      <el-form-item label="">{{error}}</el-form-item>
       <el-form-item label="">
         <span v-for="e in err_list">{{e}}&nbsp;&nbsp;</span>
       </el-form-item>
    </el-form>
    <br/>

    <el-table
      :data="list"
      style="width: 100%"
      border
      size="mini"

    >



      <el-table-column prop="apply_time" label=""></el-table-column>
      <el-table-column prop="adviser" label=""></el-table-column>
      <el-table-column prop="target_school" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="pro_class_level" label=""></el-table-column>
      <!--<el-table-column prop="public_class" label=""></el-table-column>-->
      <el-table-column prop="due_year" label=""></el-table-column>
      <el-table-column prop="qq" label="qq"></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>

   <el-table-column prop="old_major" label=""></el-table-column>
      <el-table-column prop="old_school" label=""></el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>

  <!--<el-table-column prop="status" label="">-->
      <!--<template scope="scope">-->
          <!--<p style="color: rgba(0,0,0,0.3)" v-if="scope.row.status =='' ">{{scope.row.status}}</p>-->
          <!--<p style="color: #1ab394" v-else>{{scope.row.status}}</p>-->
      <!--</template>-->
  <!--</el-table-column>-->

      <!--<el-table-column label="" width="250">-->
        <!--<template scope="scope">-->
          <!--<el-button-group>-->
            <!--<el-button type="danger" @click="retry(scope.row)" size="mini"></el-button>-->

          <!--</el-button-group>-->
        <!--</template>-->
      <!--</el-table-column>-->
    </el-table>
  </div>
</template>
<script>
  import request from 'axios'
  import Vue from 'vue'
  let loading
  var basic = require('../stableDict')
  export default {
    name: 'teacher-item',
    data() {
      return {
        list:[],

        error:0,
        success:0,
        total:0,
        err_list:[]
      }
    },
    methods: {
      download_template() {
        location.href = '/v1/excel/student_template'
      },
      start_loading() {
        loading = this.$loading({
          lock: true,
          text: '...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.1)'
        });
      },
      end_loading() {
        loading.close()
      },
      retry(){

      },

      onchange(f) {
        var that = this
        let file = f.target.files[0], name = file.name,reader = new FileReader();
        reader.onload = function (e) {
          let data = e.target.result;
          let wb = XLSX.read(data, {type: "binary"});
          let list = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])
          list = list.map(function (obj) {
            console.log(obj,'---')

            obj.apply_time = obj['']
            obj.adviser = obj['']


            let index = basic.schoolMap[0].indexOf(obj[''])

            obj['target_school'] = basic.schoolMap[1][index]
            console.log(obj.target_school,basic.schoolMap[obj['']])
            obj.name = obj['']
            obj.pro_class_level = obj['']
            // obj.public_class = obj['']
            obj.due_year = obj['']
            obj.qq = obj['QQ']
            obj.mobile = obj['']
            obj.old_major = obj['']
            obj.old_school = obj['']
            obj.remark = obj['']

            delete obj['']
            delete obj['']
            delete obj['']
            delete obj['']
            delete obj['']
            delete obj['']
            delete obj['']


            delete obj['']
            delete obj['']
            delete obj['']
            delete obj['']
            obj.status = ''
            return obj
          })

          that.list = list
          that.total = that.list.length
          that.success =0
          that.error = 0
        };
        reader.readAsBinaryString(file);
      },

      async upload(){
        var that = this
         let r = function (i) {
         request.post('/v1/excel/student_upload',that.list[i]).then(function (res) {
            that.success = that.success +1
          }).catch(function (e) {
           that.error=that.error+1
           that.error_list.push(that.list[i].name)
         })
        }
        for(var i=0;i<this.list.length;i++){
            r(i)
        }

      }

    },
    async mounted() {


    }
  }
</script>
<style soped>
  a {
    color: #1ab394;

  }
</style>



